<template>
    <div id="moblie">
          <template v-if="deviceType == 'wx'|| deviceType == 'qq'">
            <div class="maskup">
                <div class="fingerWrap moveAnimate">
                    <img src="../assets/img/moblie/finger.png" class="finger popingAnimate" alt="finger">
                </div>
                <div class="wixinDirection">
                    <p class="mr0">请点击右上角</p>
                    <p class="mr0">选择“<span class="link">在浏览器中打开</span>”</p>
                </div>
            </div>
        </template>
            <div class="moblieContent">
                <header>
                    <img
                        class="logo"
                        :src="require(`../assets/img/moblie/`+pathId+`/logo.png`)"
                        :alt="pathId+'logo'"
                    >
                    <div class="headerInfo">
                        <h3 class="lotteryTitle mr0">{{title}}</h3>
                        <p>大数据分析、实时精准推送各种玩法数据的应用工具</p>
                    </div>
                    <template v-if="enterHelpPage == true">
                        <template v-if="downloadHelper == true">
                            <div class="downloadContent">
                                <a
                                    href="#"
                                    class="downloadBtn iosDownload"
                                    @click="intoSetting"
                                >立即信任</a>
                            </div>
                        </template>
                        <template v-else-if="downloadHelper == false">
                            <p class="downloadLoading">桌面正在下载...</p>
                            <div class="downloadContent">
                                <div class="downloadInfoBlock">
                                    <div class="purpleTitle purpleBg">下载提示</div>
                                    <p>如果苹果版APP无法使用时，请进入彩世界助手下载最新版APP。如果IOS系统提示无法下载，请重试。请卸载旧版本APP，再到彩世界助手下载安装最新APP。</p>
                                </div>
                            </div>
                        </template>
                    </template>
                    <template v-else>
                        <div class="downloadContent">
                            <template v-if="deviceType == 'wx'|| deviceType == 'qq'">
                                <a href="#" class="downloadBtn">微信/QQ内无法下载应用</a>
                            </template>
                            <template v-if="deviceType == 'ios'">
                                <a
                                    href="#"
                                    class="downloadBtn purpleBg"
                                    style="margin-top: 15px;"
                                    @click="iosDownload"
                                >
                                    <!-- IOS版下载 -->
                                    立即下载
                                </a>
                            </template>
                            <template v-if="deviceType == 'android'">
                                <a
                                    href="#"
                                    class="downloadBtn purpleBg"
                                    style="margin-top: 15px;"
                                    @click="androidDownload"
                                >
                                    <!-- 安卓版下载 -->
                                    立即下载
                                </a>
                            </template>
                        </div>
                    </template>
                </header>
                <section :class="(downloadHelper == true)&&'downloadHelper'">
                    <template v-if="enterHelpPage == false">
                        <div class="bannerContent">
                            <p class="mr0">应用介绍</p>
                            <div data-simplebar="" data-simplebar-auto-hide="false" class="banner purple moblie">
                                <ul
                                    ref="bannerList"
                                    class="bannerList"
                                    :style="{width:((returnSlideW*slides)+140) +'px'}"
                                >
                                    <li v-for="(slide,i) in slides" :key="i">
                                        <img
                                            :src="require(`../assets/img/moblie/`+pathId+`/banner${slide}.png`)"
                                            alt="banner"
                                        >
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div class="bottomContent">
                            <p>{{info}}</p>
                            <div class="dwTitle">
                                <span>快去下载吧！</span>
                            </div>
                        </div>
                    </template>
                    <div v-show="enterHelpPage == true" class="flowContent">
                        <div class="installFlow">
                            <div class="title">
                                <span>操作流程</span>
                            </div>
                            <ol class="flowList">
                                <li>
                                    <p>点击上方“立即下载”按钮下载APP</p>
                                </li>
                                <li>
                                    <p>微信进入此页，选择[请点击右上角选择“在Safari中打开”]。若您直接在“在Safari中打开”页面，请直接安装。</p>
                                    <img src="../assets/img/moblie/flow1.png" alt="flow1">
                                </li>
                                <li>
                                    <p>等待桌面上安装成功后，您需再进行一步“信任”操作即可使用。</p>
                                    <img src="../assets/img/moblie/flow2.png" alt="flow2">
                                    <span>点击[设置]</span>
                                    <img src="../assets/img/moblie/flow3.png" alt="flow3">
                                    <span>点击[通用]</span>
                                    <img src="../assets/img/moblie/flow4.png" alt="flow4">
                                    <span>点击[设备管理]</span>
                                    <img src="../assets/img/moblie/flow5.png" alt="flow5">
                                    <span>点击[企业级应用]</span>
                                    <img
                                        :src="require(`../assets/img/moblie/`+pathId+`/helper1.png`)"
                                        alt="helper1"
                                    >
                                    <span>点击证书[*********]</span>
                                    <img
                                        :src="require(`../assets/img/moblie/`+pathId+`/helper2.png`)"
                                        alt="helper2"
                                    >
                                    <span>最后一步 选择[信任]</span>
                                    <img
                                        :src="require(`../assets/img/moblie/`+pathId+`/helper3.png`)"
                                        alt="helper3"
                                    >
                                </li>
                                <li>
                                    <p>恭喜您成功下载！</p>
                                </li>
                            </ol>
                        </div>
                    </div>
                </section>
            </div>
        </div>
</template>
<style scoped>
    @import "../assets/style/detail.css";
</style>
<script>
export default {
    name:'moblieDetail',
    data(){
        return{
            slides: 4,
            slideW: 200,
            enterHelpPage: false,
            downloadHelper: false,
            iosHelperlink: "https://sy.30oy.com/sychat/helper.mobileconfig",
            Ioslink:
                "itms-services://?action=download-manifest&url=https://sy.30oy.com/sychat/ios.plist",
            iosVerification: "https://sy.30oy.com/sychat/xinren.mobileprovision",
            title: "",
            info: "",
            pathId: "liuhe",
            iosdownload: false,
            deviceType: ""
        }
    },
    mounted() {
        let url = window.location.href;
        let urlArr = url.split("/");
        let result = urlArr[urlArr.length - 1].split(".");
        let urlString = new URL(url);
        let checkdownload = this.isEmpty(this.getQueryParams(url))
        // console.log('QueryParams',this.getQueryParams(url));
        // console.log('checkdownload',checkdownload);
        this.pathId = result[0];
        this.detectdDevice();
        this.scrollToTop();
        this.title = this.$store.getters.returnTitle(this.pathId);
        this.info = this.$store.getters.returnInfo(this.pathId);
        if (this.pathId == "liuhe") {
            this.slides = 4;
        } else {
            this.slides = 5;
        }
        if (checkdownload == false) {
            this.iosDownload();
        }
  },
  computed: {
    returnSlideW: function() {
      return this.slideW;
    }
  },
   methods: {
    getQueryParams(qs){
      qs = qs.split('+').join(' ');
      var params = {},
          tokens,
          re = /[?&]?([^=]+)=([^&]*)/g;

      while (tokens = re.exec(qs)) {
          params[decodeURIComponent(tokens[1])] = decodeURIComponent(tokens[2]);
      }

      return params;
    },
    isEmpty(obj) {
        for(var key in obj) {
            if(obj.hasOwnProperty(key))
                return false;
        }
        return true;
    },
    iosDownload(e) {
      if (e) {
        e.preventDefault();
      }
      var _this = this;
      this.enterHelpPage = true;
       setTimeout(function(){
          let dwArray = window.download;
          //console.log("load delay", window.download);
         for (let i = 0; i < dwArray.length; i++) {
          if (_this.pathId == dwArray[i].name) {
            // alert('测试用无下载连结 '+this.deviceType+'（'+dwArray[i].name+'）');
            window.location.href = dwArray[i].iosLink;
          }
        }
      },1000);
    },
    androidDownload(e) {
      if (e) {
        e.preventDefault();
      }
      let dwArray = window.download;
      for (let i = 0; i < dwArray.length; i++) {
        if (this.pathId == dwArray[i].name) {
          // alert('测试用无下载连结 '+this.deviceType+'（'+dwArray[i].name+'）');
          window.location.href = dwArray[i].androidLink;
        }
      }
    },
    downloadhelpTool(e) {
      if (e) {
        e.preventDefault();
      }
      var _this = this;

      _this.downloadHelper = true;
      window.location.href = _this.iosHelperlink;
    },
    intoSetting(e) {
      var _this = this;
      // var downloadlink = 'https://sy.30oy.com/sychat/helper.mobileconfig';
      if (e) {
        e.preventDefault();
      }

      window.location.href = _this.iosVerification;
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
    detectdDevice() {
      var BrowserVersion = window.GLOBAL.browser.versions;
      var nua = navigator.userAgent;
      var is_android =
        nua.indexOf("Mozilla/5.0") > -1 &&
        nua.indexOf("Android ") > -1 &&
        nua.indexOf("AppleWebKit") > -1 &&
        !(nua.indexOf("Chrome") > -1);
      if (BrowserVersion.android == true || is_android == true) {
        this.currentMoblie = true;
        if (BrowserVersion.weChat == true) {
          this.deviceType = "wx";
          this.$store.commit("sendDeviceType", "wx");
        } else if (
          /MQQBrowser/i.test(navigator.userAgent) &&
          /\sQQ/i.test(navigator.userAgent.split("MQQBrowser"))
        ) {
          this.deviceType = "qq";
          this.$store.commit("sendDeviceType", "qq");
        } else {
          this.deviceType = "android";
          this.$store.commit("sendDeviceType", "android");
        }
      } else if (
        BrowserVersion.ios == true ||
        BrowserVersion.iPhone == true ||
        BrowserVersion.iPad == true
      ) {
        this.currentMoblie = true;

        if (BrowserVersion.weChat == true) {
          this.deviceType = "wx";
          this.$store.commit("sendDeviceType", "wx");
        } else if (/\sQQ/i.test(navigator.userAgent)) {
          this.deviceType = "qq";
          this.$store.commit("sendDeviceType", "qq");
        } else {
          this.deviceType = "ios";
          this.$store.commit("sendDeviceType", "ios");
        }
      }
    }
  }
}
</script>
