    </div>
    <a @click="gotoTop" class="icon-btn">
      <div class="icon icon-top"></div>
      顶部
    </a>
  </div>
</template>
<script>
import { setTimeout } from 'timers';
import QrcodeVue from 'qrcode.vue'
import _ from 'lodash';
export default {
    props:{
      qrcodeUrl:{
        type:String,
        default:''
      }
    },
    data(){
        return {
            showSideBar:false,
            showInfoBlock1:false,
            showInfoBlock2:false,
            showInfoBlock3:false
        }
    },
    components:{
      QrcodeVue
    },
    mounted() {
        var _this = this;
        setTimeout(function(){
            _this.showSideBar = true;
        },300);
        const sideBar = this.$refs.root;
         const adjustBottomOfSideBar = () => {
            sideBar.style.position = 'fixed';
            sideBar.style.top = window.innerHeight / 2 - 300 / 2 + 'px'; // middle of screen
        };
        window.addEventListener('resize', _.debounce(adjustBottomOfSideBar, 50));
        adjustBottomOfSideBar();
    },
    methods:{
        gotoTop() {
            window.scrollTo({
            left: 0,
            top: 0,
            behavior: 'smooth'
            });
        }
    }
}
</script>
